import {Component} from '@angular/core';
import { Route,Routes,Router,ROUTER_DIRECTIVES} from '@angular/router';

//import { HeroService } from '../hero-portal/service/hero.service';
import { HeroesComponent } from '../hero-portal/hero/heroes.component';
import { HeroDetailComponent } from '../hero-portal/hero/hero-detail.component';
import { DashboradComponent } from '../hero-portal/dashborad/dashborad.component';
import { HeroFormComponent } from '../hero-portal/hero/hero-form.component';

@Component({
    selector:'my-enter-hero',
    template:`
             <h1>{{title}}</h1>
             <hr>
             <nav>
             <a [routerLink]="['dashborad']">Dashborad</a> 
             <a [routerLink]="['hero']">Heroes</a>
             <a [routerLink]="['hero-form']">+</a>
             </nav>
             <router-outlet></router-outlet>
    `,
    styleUrls:["app/app.component.css"],
    directives:[ROUTER_DIRECTIVES]
    // ,
    // providers:[HeroService]
})
@Routes([
new Route({ path:'/dashborad', component:DashboradComponent}),
new Route({ path:'/hero',component:HeroesComponent}),
new Route({ path:'/detail/:id', component:HeroDetailComponent}),
new Route({ path:'/hero-form', component:HeroFormComponent})
])
export class HeroPortalComponent {
    title='Tour of Heroes';
    constructor(private _rtr:Router){
    }
}
